@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;700&display=swap);
/* Google Font import */

:root {
  --very-dark: #1e202a;
  --dark: #1f212e;
  --dark-blue: #252a41;
  --light-blue: #8b97c6;
  --primary-text: #fff;
  --secondary-text: #363e57;
  --mint: #1db489;
  --red: #dc414c;
  --sm-heading: 1.5rem;
  --sm-secondary: 1.3rem;
  --sm-info: 1rem;
}

/* Basic setup */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Oswald", sans-serif;
  height: 100vh;
  line-height: 1.6;
  color: #fff;
  color: var(--primary-text);
  background-color: #1e202a;
  background-color: var(--very-dark);
  overflow-x: hidden;
}

img {
  width: 100%;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #fff;
  color: var(--primary-text);
}

/* utilities */

.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 1.5rem;
}

main {
  width: calc(100vw - 23vw);
  position: relative;
  left: 23vw;
  background-color: #252a41;
  background-color: var(--dark-blue);
}

section {
  animation: appear 0.9s linear;
}

@keyframes appear {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 575.98px) {
  .container {
    max-width: 540px;
    padding: 0 20px;
  }
}

/* // Small devices (landscape phones, less than 768px) */
@media (max-width: 767.98px) {
  .container {
    max-width: 720px;
  }

  main {
    width: 100vw;
    position: relative;
    height: 100%;
    left: 0;
    background-color: #252a41;
    background-color: var(--dark-blue);
  }

  @keyframes appear {
    0% {
      opacity: 0;
      transform: translateY(-30px);
    }

    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

/* // Medium devices (tablets, less than 992px) */
@media (max-width: 991.98px) {
  .container {
    max-width: 960px;
  }
}

