.about {
  width: 100%;
  height: 100vh;
}

.about_info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
}

.about_info h1 {
  font-size: 30px;
  text-transform: uppercase;
  text-align: center;
  width: 100%;
  padding: 10px;
  font-weight: 800;
}

.about_info h2 {
  font-size: 24px;
  font-weight: 200;
  padding: 5px 0;
  color: var(--mint);
}

.about_info p:first-of-type {
  padding: 0;
  width: 100%;
  text-align: center;
  letter-spacing: 1px;
}

.about_info p {
  line-height: 1.6;
  letter-spacing: 0.5px;
  font-weight: 300;
  font-size: 20px;
  padding: 0 20px;
}

.special_text {
  color: var(--red);
  font-weight: 400;
}

.btn {
  background-color: var(--red);
  color: #fff;
  padding: 8px 12px;
  font-weight: 800;
  text-transform: uppercase;
  border-radius: 50px;
  font-size: 14px;
  align-self: center;
  margin-top: 10px;
}

@media (max-width: 1199.98px) {
  .about {
    height: 100%;
  }
}

@media (max-width: 767.98px) {
  .about {
    padding-bottom: 80px;
  }
}

@media (max-width: 575.98px) {
  .about_info h1 {
    font-size: var(--sm-heading);
  }

  .about_info h2 {
    font-size: var(--sm-secondary);
  }

  .about_info p {
    font-size: var(--sm-info);
    padding: 0;
  }

  .btn {
    font-size: 12px;
  }
}
